"use client"; // Next.js (App Router)에서 클라이언트 컴포넌트로 설정

import { useState } from "react";

const OCR = () => {
  const [image, setImage] = useState<File | null>(null);
  const [text, setText] = useState<string>("");
  const [loading, setLoading] = useState(false);

  /**
   * TODO(developer): Uncomment the following line before running the sample.
   */
  // const fileName = 'Local image file, e.g. /path/to/image.png';

  // Performs text detection on the local file

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) {
      console.log(e.target.files?.[0]);
      setImage(e.target.files[0]);
    }
  };

  const extractText = async () => {
    if (!image) return;
    setLoading(true);

    try {
      const response = await fetch("/api/ocr", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ image }),
      });

      if (!response.ok) {
        throw new Error("OCR API 요청 실패");
      }

      const { text } = await response.json();
      setText(text);
    } catch (error) {
      console.error("텍스트 추출 오류:", error);
      setText("OCR 처리 중 오류가 발생했습니다.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-center gap-4 ">
      <input type="file" accept="image/*" onChange={handleImageChange} />
      <button
        onClick={extractText}
        className="border-2 border-gray px-4 py-2 font-sans text-xs font-bold text-center text-gray-900 uppercase align-middle transition-all rounded-md select-none hover:bg-lightgray active:bg-gray-900/20"
        disabled={loading}
      >
        {loading ? "Processing..." : "Extract Text"}
      </button>
      {/* {text && (
        <textarea
          className="w-full p-2 border rounded-md"
          rows={5}
          readOnly
          value={text}
        />
      )} */}
    </div>
  );
};

export default OCR;
